@charset "UTF-8";
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block; }

body {
  line-height: 1; }

ol,
ul {
  list-style: none; }

blockquote,
q {
  quotes: none; }

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

*,
*::before,
*::after {
  box-sizing: border-box; }

* {
  font-family: "微軟正黑體" !important;
  box-sizing: border-box; }

img {
  vertical-align: top; }

.container {
  width: 67.5%;
  margin: 0 auto;
  padding-left: 12px;
  padding-right: 12px;
  box-sizing: content-box; }

.container-s {
  width: 636px;
  margin: 0 auto; }

.flex {
  display: flex; }

.justify-space-between {
  justify-content: space-between; }

.align-center {
  align-items: center; }

h2 {
  font-size: 64px;
  line-height: 96px; }

h3 {
  font-size: 48px;
  line-height: 72px; }

h4 {
  font-size: 36px;
  line-height: 54px; }

h5 {
  font-size: 24px;
  line-height: 36px; }

h6 {
  font-size: 16px;
  line-height: 24px; }

body {
  font-size: 16px;
  line-height: 16px; }

header .menu {
  line-height: 1.85rem;
  font-size: 1.25rem;
  gap: 80px; }

footer {
  padding: 48px 0; }
  footer .divider {
    border-bottom: 1px solid #fff;
    margin: 32px 0; }
  footer .top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap; }
  footer .menu {
    gap: 48px; }
  footer .social-icons {
    gap: 32px; }
    footer .social-icons img {
      width: 48px; }
  footer .contact-ways {
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 8px;
    color: #fff;
    font-size: 36px;
    line-height: 54px;
    margin-top: 24px; }
    footer .contact-ways i {
      margin-right: 20px; }
  footer .tnc {
    color: #fff;
    display: flex;
    justify-content: space-between; }

header,
footer {
  background-color: #AA0601; }
  header a,
  footer a {
    text-decoration: none;
    color: #fff; }

.logo {
  width: 80px;
  margin: 16px 0; }

.menu {
  display: flex; }

@media (max-width: 992px) {
  .container {
    width: initial;
    padding: 0 36px; }
  header .menu {
    gap: 64px; } }

@media (max-width: 767px) {
  .container {
    padding: 0 14px 0 13px; }
  .header {
    flex-wrap: wrap;
    padding: 0;
    margin: 0; }
    .header h1,
    .header nav {
      width: 100%;
      text-align: center; }
    .header .menu {
      line-height: 1.5rem;
      font-size: 1rem;
      gap: 0;
      flex-wrap: wrap; }
    .header nav li {
      width: 50%;
      border: 1px solid #fff; }
      .header nav li:hover {
        background-color: #c30701; }
      .header nav li:active {
        background-color: #910501; }
    .header nav a {
      display: block;
      width: 100%;
      padding: 12px 0; }
  footer {
    padding: 24px 0; }
    footer * {
      font-size: 16px;
      line-height: 24px; }
    footer .top {
      flex-direction: row-reverse;
      align-items: start; }
    footer .shortcuts {
      display: none; }
    footer .social-icons {
      gap: 8px; }
      footer .social-icons img {
        width: 24px; }
    footer .contact-ways {
      width: initial;
      margin-top: 0; }
    footer .tnc {
      flex-direction: column;
      gap: 8px; }
      footer .tnc .menu {
        flex-direction: column;
        gap: 8px; } }

section {
  padding: 80px 0; }

.hero-banner {
